	module.exports = {"data":{"currentPage":{"html":"<h1>Ship it!</h1>\n<p>What greater joy that to push your hardwork out to the world. üòçüòçüòç</p>\n<h2>Zeit Now, Heroku or Glitch</h2>\n<p>Probot guide again shines here. This was a breeze and the guide provided was spot on! So I won't bore you. Just read and follow along with the guide on the <a href=\"https://probot.github.io/docs/deployment/\">Probot Site</a>.</p>\n<p>I initially tried Glitch, mostly because I was not quite familiar with it. It was a seamless experience and my app was up and live in a few minutes. However, maybe I was missing something, but if I made any change to my local copy, I could not figure out how to make the change to through to Glitch short of copying it over. So I quickly ditched this route. And went with now, mostly because I use it currently. But Glitch was still a very nice new discovery for me.</p>\n<h2>And we are LIVE!</h2>\n<p>The deployment with Zeit was pretty straightforward. And we can say out Bot found a good home. Remember to update the Webhook URL both on your local env file and in the Github App Settings Page.</p>\n<p>I found that using the env setting in now.json, it was not properly reading the .data.private-key file. So I just created a snippet in a DEPLOY.md file, that I added to gitignore. Then run now, with the snippets that has all the ENV variables specified in the Probot guide.</p>\n<pre><code class=\"language-bash\">now -e APP_ID=XXXXX \\\n-e WEBHOOK_SECRET=development \\\n-e NODE_ENV=production \\\n-e PRIVATE_KEY_BASE64=\"$(cat ./.data/private-key.pem | base64)\" \\\n-e LOG_LEVEL=trace\n</code></pre>\n<p>After which I addded an alias like so:</p>\n<p><code>now alias set https://gitty-probot-hsmuipeqzu.now.sh https://gitty-probot.now.sh</code></p>\n<p>And then now scale to keep our bot up and running</p>\n<p><code>now scale https://gitty-probot.now.sh 1</code></p>\n<p>To ever remove a deployment if you run into an error saying you have too many instances up, run:</p>\n<p><code>now rm https://gitty-probot-eitbbbhqts.now.sh</code></p>\n<p>And again, remember to update Github app settings. Since I am using an alias, I only have to do this once.</p>\n<p><img src=\"/images/probot-05-now-deployment-live.png\" alt=\"Gitty Bot Live on Zeit NOW\"></p>\n<p>And that's all. How cool! Our very First Bot üéâüéâüéâ. Hopefully, one of many more.</p>\n<div>\n  Shall we say this was a success? Ok. Let's wrap this up! <span>üéâ<span>\n</div>","frontmatter":{"title":"Deployment","description":"Hello from the other side!","commit":null},"fields":{"slug":"/probot/en/deployment/","chapter":"deployment","framework":"probot","language":"en"}},"site":{"siteMetadata":{"title":"Probot Gitty Test","toc":["getting-started","create-probot-app","core-development","test-fixture-setup","writing-some-tests","deployment","conclusion"],"languages":["en"],"githubUrl":"https://github.com/nnennajohn/gitty-probot-test","codeGithubUrl":"https://github.com/nnennajohn/gitty-probot","siteUrl":"https://weekendprojects.io/"}},"pages":{"edges":[{"node":{"frontmatter":{"tocTitle":"Create Robot App","title":"Create Robot App","description":"Bootstrapping our App With Create Probot App"},"fields":{"slug":"/probot/en/create-probot-app/","chapter":"create-probot-app"}}},{"node":{"frontmatter":{"tocTitle":"Conclusion","title":"Conclusion","description":"And its a wrap"},"fields":{"slug":"/probot/en/conclusion/","chapter":"conclusion"}}},{"node":{"frontmatter":{"tocTitle":"Core Development","title":"Core Development","description":"Let's really get this party started!"},"fields":{"slug":"/probot/en/core-development/","chapter":"core-development"}}},{"node":{"frontmatter":{"tocTitle":"Testing Setup","title":"Testing Setup","description":"Let's setup our test and add some text fixtures"},"fields":{"slug":"/probot/en/test-fixture-setup/","chapter":"test-fixture-setup"}}},{"node":{"frontmatter":{"tocTitle":"Deployment","title":"Deployment","description":"Hello from the other side!"},"fields":{"slug":"/probot/en/deployment/","chapter":"deployment"}}},{"node":{"frontmatter":{"tocTitle":"Getting started","title":"Getting started","description":"Getting started by going through Probot Docs and Github Webhook API"},"fields":{"slug":"/probot/en/getting-started/","chapter":"getting-started"}}},{"node":{"frontmatter":{"tocTitle":"Writing Some Tests","title":"Writing Some Tests","description":"Testing the testing the testing!"},"fields":{"slug":"/probot/en/writing-some-tests/","chapter":"writing-some-tests"}}}]}},"pathContext":{"slug":"/probot/en/deployment/","framework":"probot","language":"en","chapter":"deployment"}}

